<template>
  <div id="app">
    <v-app id="inspire">
      <v-card>
        <v-card-title>
        </v-card-title>
        <v-data-table
            :headers="headers"
            :items="discs"
            :search="search"
            item-key="title"
            v-model="selected"
            :show-select="selectable"
        >
          <template v-slot:footer="{  }">
            <span v-if="selected.length > 0">
            <v-label>Selected {{selected.length}} items: </v-label>
              <v-select v-model="selectedStatus" :items="stati" label="Change Status...">
              </v-select>
              <v-text-field v-if="selectedStatus === 'loaned'" v-model="loanOutTo" label="Loan out to..." hide-details="auto"></v-text-field>
              <v-btn
                  color="primary"
                  dark
                  class="mb-2"
                  @click="updateStatus()"
              >
                Change
              </v-btn>
            <v-btn
                color="error"
                dark
                class="mb-2"
                @click="deleteSelected()"
            >
              Delete
            </v-btn>

            </span>
          </template>
          <template v-slot:item.actions="{ item }">
            <v-icon
                small
                class="mr-2"
                @click="editItem(item)"
            >
              mdi-pencil
            </v-icon>
            <v-icon
                small
                @click="deleteItem(item)"
            >
              mdi-delete
            </v-icon>
          </template>
          <template v-slot:top>
            <v-toolbar
                flat
            >
              <v-dialog
                  v-model="dialog"
                  max-width="500px"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-btn
                      color="primary"
                      dark
                      class="mb-2"
                      v-bind="attrs"
                      v-on="on"
                  >
                    New Disc
                  </v-btn>
                  <v-text-field
                      v-model="search"
                      append-icon="mdi-magnify"
                      label="Search"
                      single-line
                      hide-details
                      class="ml-2"
                  ></v-text-field>
                </template>
                <v-card>
                  <v-card-title>
                    <span class="headline">{{ formTitle }}</span>
                  </v-card-title>

                  <v-card-text>
                    <v-container>
                      <v-row>
                        <v-col
                            cols="12"
                            sm="6"
                            md="4"
                        >
                          <v-text-field
                              v-model="editedItem.title"
                              label="Disc Title"
                          ></v-text-field>
                        </v-col>
                        <v-col
                            cols="12"
                            sm="6"
                            md="4"
                        >
                          <v-text-field
                              v-model="editedItem.year"
                              label="Year"
                          ></v-text-field>
                        </v-col>
                        <v-col
                            cols="12"
                            sm="6"
                            md="4"
                        >
                          <v-text-field
                              v-model="editedItem.status"
                              label="Status"
                          ></v-text-field>
                        </v-col>
                      </v-row>
                    </v-container>
                  </v-card-text>

                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn
                        color="blue darken-1"
                        text
                        @click="close"
                    >
                      Cancel
                    </v-btn>
                    <v-btn
                        color="blue darken-1"
                        text
                        @click="save"
                    >
                      Save
                    </v-btn>
                  </v-card-actions>
                </v-card>
              </v-dialog>
              <v-dialog v-model="dialogDelete" max-width="500px">
                <v-card>
                  <v-card-title class="headline">Are you sure you want to delete this item?</v-card-title>
                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="blue darken-1" text @click="closeDelete">Cancel</v-btn>
                    <v-btn color="blue darken-1" text @click="deleteItemConfirm">OK</v-btn>
                    <v-spacer></v-spacer>
                  </v-card-actions>
                </v-card>
              </v-dialog>
            </v-toolbar>
          </template>
        </v-data-table>
      </v-card>
    </v-app>
  </div>
</template>

<script>

export default {
  name: "DiscCollection",
  props: ['adminAccess'],
  data () {
    return {
      stati: ["available","loaned","sold"],
      // group status changes
      selectedStatus: "available",
      // The name of the person the disc was loaned out to
      loanOutTo: "",
      selected: [],
      search: '',
      dialog: false,
      dialogDelete: false,
      headers: [],
      basicHeaders: [
        {
          text: 'Title',
          align: 'start',
          value: 'title',
        },
        { text: 'Year', value: 'year' }],
      adminHeaders: [
        { text: 'Status', value: 'status' },
        { text: 'Loaned out to', value: 'to' },
        { text: 'Actions', value: 'actions', sortable: false }
      ],
      discs: [],
      editedIndex: -1,
      editedItem: {
        title: '',
        year: 0,
        status: '',
        to: ''
      },
      defaultItem: {
        title: '',
        year: 0,
        status: '',
        to: ''
      },
    }
  },
  computed: {
    formTitle () {
      return this.editedIndex === -1 ? 'New Disc' : 'Edit Disc'
    },
    selectable () {return this.adminAccess}
  },
  watch: {
    dialog (val) {
      val || this.close()
    },
    dialogDelete (val) {
      val || this.closeDelete()
    },
    adminAccess (val){
      this.initHeaders(val);
    }
  },
  created () {
    this.initialize()
  },
  methods: {
    initHeaders(adminAccess){
      if(adminAccess)this.headers = this.basicHeaders.concat(this.adminHeaders)
      else this.headers = this.basicHeaders
    },
    initialize () {
      this.initHeaders(this.adminAccess);
      this.discs = [
        {
          title: "The Wizard of Oz",
          year: 1939,
          status: "available",
          to: ""
        },
        {
          title: "Gone With The Wind",
          year: 1939,
          status: "available",
          to: ""
        },
        {
          title: "Casablanca ",
          year: 1942,
          status: "sold",
          to: ""
        },
        {
          title: "Breakfast At Tiffany's",
          year: 1961,
          status: "loaned",
          to: "Mike"
        },
        {
          title: "Lawrence of Arabia",
          year: 1962,
          status: "loaned",
          to: "Lee"
        },
        {
          title: "West Side Story",
          year: 1961,
          status: "sold",
          to: ""
        },
        {
          title: "Citizen Kane",
          year: 1941,
          status: "available",
          to: ""
        },
        {
          title: "Psycho ",
          year: 1960,
          status: "available",
          to: ""
        },
        {
          title: "The Women",
          year: 1939,
          status: "loaned",
          to: "Mum"
        },
        {
          title: "Laura",
          year: 1944,
          status: "sold",
          to: ""
        },
        {
          title: "The Best Years of Our Lives",
          year: 1946,
          status: "available",
          to: ""
        },
        {
          title: "Rebel Without a Cause",
          year: 1955,
          status: "loaned",
          to: "I don't remember"
        },
        {
          title: "The Godfather",
          year: 1972,
          status: "available",
          to: ""
        }
      ]
    },
    updateStatus(){
      for (const item of this.selected) {
        console.log("selected:",item)
        const idx = this.discs.indexOf(item)
        const e = this.discs[idx]
        e.status = this.selectedStatus
        e.to = this.loanOutTo;
        this.discs.splice(idx,1,item)
      }
    },
    deleteSelected(){
      for (const item of this.selected) {
        const idx = this.discs.indexOf(item);
        this.discs.splice(idx, 1);
      }
      this.selected = [];
    },
    editItem (item) {
      this.editedIndex = this.discs.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialog = true
    },
    deleteItem (item) {
      this.editedIndex = this.discs.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialogDelete = true
    },
    deleteItemConfirm () {
      this.discs.splice(this.editedIndex, 1)
      this.closeDelete()
    },
    close () {
      this.dialog = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },
    closeDelete () {
      this.dialogDelete = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },
    save () {
      if (this.editedIndex > -1) {
        Object.assign(this.discs[this.editedIndex], this.editedItem)
      } else {
        this.discs.push(this.editedItem)
      }
      this.close()
    },
  }
}
</script>

<style scoped>

</style>